.action-container {
  margin-top: 20px;
}

.back-next-btns .back-btn {
  margin-right: 20px;
}

.height-weight-wrapper .feet-field {
  margin-right: 20px;
}

.height-weight-wrapper .inches-field {
  margin-right: 20px;
}

.radio-wrapper .radio-group {
  display: block;
  padding-left: 5px;
  padding-top: 10px;
}

.radio-wrapper .radio-group .radio-btn {
  display: inline-block;
}

.radio-wrapper .radio-group .no-radio {
  margin-right: 20px;
}

.radio-image-wrapper {
  display: inline-block;
  margin-right: 25px;
}

.radio-image {
  width: 100px;
  height: 120px;
  background-size: contain;
  border-radius: 8px;
  box-shadow: 0px 0px 1px 1px #00000021;
  margin-bottom: 10px;
}

.incision-image {
  width: 300px;
  height: 360px;
  background-size: contain;
  border-radius: 8px;
  box-shadow: 0px 0px 1px 1px #00000021;
  margin-bottom: 10px;
}